import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import NavigationBar from './Components/NavigationBar';
import HomePage from './HomePage';
import AppsPage from './AppsPage';
import OpenSourceProjectsPage from './OpenSourceProjectsPage';
import CVPage from './CVPage';
import BlogPage from './BlogPage';
import Error404Page from './Error404Page';
import IsShopOpenYetPage from './IsShopOpenYet';
import PortersPage from './Clues/Porters';
import EL69Page from './Clues/EL69';
import ShackPage from './Clues/Shack';
import WelfareBoxPage from './Clues/WelfareBox';
import SutherlandKitchenPage from './Clues/SutherlandKitchen';
import SutherlandFreezerPage from './Clues/SutherlandFreezer';
import ELKitchenPage from './Clues/ELKitchen';
import NetballCourtPage from './Clues/NetballCourt';
import LibraryPage from './Clues/Library';
import ArchiePage from './Clues/Archie';

export default class App extends Component {
  render() {
    return (
      <Router>
        <div>
          <NavigationBar />
          <Switch>
            <Route exact path="/" component={HomePage} />
            <Route path="/apps/:appId?" component={AppsPage} />
            <Route path="/open-source/:projectId?" component={OpenSourceProjectsPage} />
            <Route path="/resume/:section?" component={CVPage} />
            <Route path="/cv/app/:appId?" component={CVPage} />
            <Route path="/cv/work/:workId?" component={CVPage} />
            <Route path="/cv/:section?" component={CVPage} />
            <Route path="/blog/:article?" component={BlogPage} />
            <Route path="/is-shop-open-yet" component={IsShopOpenYetPage} />
            <Route path="/clues/porters" component={PortersPage} />
            <Route path="/clues/archie" component={ArchiePage} />
            <Route path="/clues/el69" component={EL69Page} />
            <Route path="/clues/shack" component={ShackPage} />
            <Route path="/clues/welfarebox" component={WelfareBoxPage} />
            <Route path="/clues/sutherlandkitchen" component={SutherlandKitchenPage} />
            <Route path="/clues/sutherlandfreezer" component={SutherlandFreezerPage} />
            <Route path="/clues/elkitchen" component={ELKitchenPage} />
            <Route path="/clues/netballcourt" component={NetballCourtPage} />
            <Route path="/clues/library" component={LibraryPage} />
            <Route component={Error404Page} />
          </Switch>
        </div>
      </Router>
    );
  }
}