import React from 'react';
import PasswordProtected from './PasswordProtected';

const defaultCounter = 20;

export default class LibraryPage extends React.Component {
  state = {
    counter: defaultCounter
  };

  onClick = (e) => {
    e.preventDefault();
    this.timerChange();
  }

  timerChange = () => {
    this.setState({counter: this.state.counter - 1});
    setTimeout(this.timerChange, 500);
  }

  render() {
    const { counter } = this.state;

    return (
      <PasswordProtected>
        <h1 className="introduction">You on your way yet?</h1>
        <h4><a href="https://www.youtube.com/watch?v=oHg5SJYRHA0">Clue 1</a></h4>
        <h4><a href="#clue2" onClick={this.onClick}>Clue 2</a></h4>
        {counter !== defaultCounter && counter >= 0 && <h4 style={{fontSize: (30 - counter) * 5 + 10 + 'px'}}>{counter}s</h4>}
        {counter < 0 && "Hope you wore a Hat"}
        <h2>Library</h2>
      </PasswordProtected>
    );
  }
}
