export default class PasswordProtected extends React.Component {
  state = {
    loggedIn: false
  }

  componentDidMount() {
    if (document.cookie.indexOf('loggedIn=true') !== -1) {
      this.setState({loggedIn: true})
    }
  }

  onClick = (e) => {
    e.preventDefault();
    const value = this.password.value;
    if (value === 'lava') {
      document.cookie = 'loggedIn=true'; 
    } else {
      console.log(':(');
    }
  }

  render() {
    const { children } = this.props;
    const { loggedIn } = this.state;

    return (
      <main className="home-page">
        <div className="home-page-content">
          {loggedIn ?
            children
          :
          <React.Fragment>
            <h1>Whats the password?</h1>
            <h2></h2>
            <input ref={password => this.password = password} type="text" style={{fontSize: 50 + 'px', textAlign: 'center'}} />
            <br />
            <h3><a href="#enter" onClick={this.onClick}>Enter</a></h3>
          </React.Fragment>
          }
        </div>
      </main>
    );
  }
}